body {
    margin: 0;
    background-color: black;
}

/*header start*/

.header {
    background-color: black;
    color: blanchedalmond;
    height: 20vh;
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
}

.header div {
    display: flex;
}

.header p {
    color: blanchedalmond;
    display: flex;
    justify-items: center;
    padding-right: 10px;
    width: 105 px;
    font-family: Arial, Helvetica, sans-serif;
}

button {
    background-color: rgb(49, 49, 49);
    color: rgb(255, 255, 255);
    font-size: 1rem;
    margin-left: 5px;
    margin-right: 5px;
    font-family: Arial, Helvetica, sans-serif;
    cursor: pointer;
    border-width: 0;
    border-radius: 50px;
    padding: 15px;
}

button:hover {
    background-color: rgb(87, 86, 80);
    color: #ff9900;
}

.slider {
    display: flex;
    justify-items: center;
    align-items: center;
    width: 200px;
}

/* endheader */

/* for bars */
.container {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    height: 80vh;
}
.bar {
    width: 10px;
    margin: 1px;
    border-radius: 1px;
}

/* endbars */
